@charset "UTF-8";
:root {
  --font-family: "Onest", sans-serif;
  --color-gray-100: #F2F2F2;
  --color-gray-200: #F0F0F0;
  --color-gray-300: #DDDBD9;
  --color--black: #000000;
  --color--white: #FFFFFF;
}

.flex {
  display: flex;
}

.flex-between {
  justify-content: space-between;
}

.flex-center {
  justify-content: center;
}

.items-center {
  align-items: center;
}

.gap-10 {
  gap: 10px;
}

.button {
  display: flex;
  height: 45px;
  padding: 13px 25px;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.button--primary {
  background-color: var(--color--black);
  color: var(--color--white);
  font-weight: 700;
  border-radius: 25px;
}

.link--primary {
  width: 45px;
  height: 45px;
  background-color: var(--color--black);
  border-radius: 100%;
  display: flex;
}

.w-100 {
  width: 100%;
}

.divider {
  height: 1px;
  background-color: rgba(0, 0, 0, 0.1);
}

.list li {
  padding-left: 20px;
  position: relative;
}
.list li::before {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: var(--color--black);
  border-radius: 100%;
  position: absolute;
  left: 0;
  top: 3px;
}

.inpt {
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  height: 35px;
  padding: 0 15px;
}
.inpt::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.title {
  font-size: 32px;
  font-weight: 900;
}

.text-center {
  text-align: center;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

h1, .h1 {
  font-size: 32px;
  font-weight: 900;
}

.color--shaded {
  color: rgba(0, 0, 0, 0.5);
}

.font-bold {
  font-weight: 700;
}

.paragraph {
  font-size: 18px;
  font-weight: 500;
}

.input {
  height: 55px;
  border-radius: 55px;
  border: 1px solid rgba(0, 0, 0, 0.35);
  padding: 0 25px;
}
.input::placeholder {
  color: rgba(0, 0, 0, 0.35);
}

@media screen and (max-width: 767.99px) {
  .title {
    font-size: 24px;
  }
  .h1 {
    font-size: 24px;
  }
  .paragraph {
    font-size: 14px;
  }
}
* {
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

.spacer {
  display: block;
  width: 100%;
}
.spacer--40 {
  height: 40px;
}
.spacer--72 {
  height: 72px;
}
.spacer--80 {
  height: 80px;
}
.spacer--120 {
  height: 120px;
}
.spacer--150 {
  height: 150px;
}
.spacer--200 {
  height: 200px;
}

body {
  font-family: var(--font-family);
  font-size: 14px;
  padding: 0;
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1524px;
  padding-left: 100px;
  padding-right: 100px;
  margin: 0 auto;
}
.container.container-sm {
  max-width: 1200px;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.2s linear;
}

p {
  margin: 0;
  padding: 0;
}

img, svg, video {
  max-width: 100%;
  max-height: 100%;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  padding: 0;
  font-family: inherit;
}

@media screen and (max-width: 1199.99px) {
  .container {
    padding-left: 70px;
    padding-right: 70px;
  }
}
@media screen and (max-width: 1024.99px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media screen and (max-width: 640.99px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.header {
  padding: 20px 0;
}
.header__wrapper {
  width: 100%;
  height: 100%;
}
.header__logo {
  width: 50px;
}
.header__nav-outer {
  width: calc(100% - 100px);
  justify-content: space-between;
  display: flex;
}
.header__nav {
  font-weight: 700;
}
.header__nav ul {
  display: flex;
}
.header__nav li {
  padding: 10px 20px;
  border-radius: 25px;
  transition: background-color 0.2s linear;
}
@media (hover: hover) {
  .header__nav li:hover {
    background-color: var(--color-gray-100);
  }
}
.header__actions {
  display: flex;
  gap: 15px;
}

.burger {
  width: 20px;
  position: relative;
}

.burger .bottom-line,
.burger .middle-line,
.burger .top-line {
  position: absolute;
  left: 0;
  right: 0;
  width: 20px;
  height: 2px;
  background-color: #000;
}

.burger .top-line {
  top: 20px;
  transition: all 0.2s ease-in-out;
}

.burger .middle-line {
  top: 28px;
  transition: all 0.2s ease-in-out;
}

.burger .bottom-line {
  top: 36px;
  transition: all 0.2s ease-in-out;
}

.open .top-line {
  transform: translate3d(0, 5px, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(45deg) skew(0deg, 0deg);
  transform-style: preserve-3d;
  transition: all 0.3s ease-in-out;
}

.open .middle-line {
  opacity: 0;
}

.open .bottom-line {
  transition: all 0.3s ease-in-out;
  transform: translate3d(0, -10.5px, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(-45deg) skew(0deg, 0deg);
  transform-style: preserve-3d;
}

.burger {
  display: none;
}

@media screen and (max-width: 1199.99px) {
  .header__actions {
    gap: 10px;
  }
  .header__nav li {
    padding: 10px;
  }
}
@media screen and (max-width: 1024.99px) {
  .header {
    position: relative;
    padding: 10px 0;
  }
  .header__nav-outer {
    position: absolute;
    background-color: var(--color--white);
    flex-direction: column;
    width: fit-content;
    padding: 30px;
    height: 100vh;
    top: 69px;
    right: -100%;
    transition: all 0.4s cubic-bezier(0.8, 0.1, 0.01, 0.04);
  }
  .header__nav-outer.active {
    right: 0;
  }
  .header__nav ul {
    flex-direction: column;
  }
  .burger {
    display: block;
  }
}
.hero {
  background-color: var(--color-gray-100);
  background-image: url("../img/hero.png");
  padding: 98px 0;
}
.hero__offer {
  max-width: 492px;
  border-radius: 25px;
