  top: 0;
  position: absolute;
}
.hero .button {
  margin-top: 49px;
}

@media screen and (max-width: 1199.99px) {
  .hero {
    padding: 70px 0;
    background-position: bottom;
  }
}
@media screen and (max-width: 767.99px) {
  .hero {
    min-height: 770px;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 25px 0;
    display: flex;
    background-position: 86%;
  }
  .hero__offer {
    width: 100%;
    margin: 0 auto;
  }
  .hero > .container {
    margin-top: auto;
  }
  .hero__offer {
    padding: 25px;
  }
  .hero__description {
    font-size: 14px;
  }
  .hero__list {
    margin-top: 25px;
  }
  .hero .divider {
    margin-top: 25px;
  }
  .hero .button {
    margin-top: 35px;
  }
}
@media screen and (max-width: 640.99px) {
  .hero {
    background-image: url("../img/hero-mobile.png");
    background-position: 80% 30%;
  }
}
@media screen and (max-width: 480.99px) {
  .hero {
    background-position: 80% 123%;
    background-size: cover;
  }
}
.calculator {
  padding: 100px 0;
  /* Включено */
  /* общая панель + анимация раскрытия (совместимо с прошлым JS) */
  /* панель-список тумблеров */
  /* панель-контролы */
  /* disabled внутри закрытой панели — уже делаем в JS,
     но визуально подсветим: */
}
.calculator__wrapper {
  gap: 57px;
}
.calculator__info {
  width: calc(35% - 57px);
}
.calculator__layer {
  width: 65%;
  background-color: var(--color-gray-100);
  padding: 35px;
  border-radius: 25px;
}
.calculator__list {
  margin-top: 50px;
  gap: 35px;
  display: flex;
  flex-direction: column;
}
.calculator__list li {
  display: flex;
  flex-direction: column;
  gap: 11px;
}
.calculator__list .list-title {
  font-weight: 700;
}
.calculator__layer .divider {
  margin-top: 25px;
}
.calculator__layer .form-inner {
  display: flex;
  gap: 25px;
}
.calculator__layer-inner {
  margin-top: 35px;
}
.calculator .input-outer {
  width: calc(50% - 15px);
  display: flex;
  flex-direction: column;
}
.calculator .input-outer label {
  font-weight: 700;
  margin-bottom: 5px;
}
.calculator .input-outer .divider {
  margin: 10px 0;
}
.calculator .form-left {
  width: calc(66.66% - 25px);
}
.calculator .form-right {
  width: 33.33%;
  background-color: var(--color--white);
  padding: 15px;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.calculator .form-right .divider {
  margin: 15px 0;
}
.calculator .form-right .subtitle {
  font-size: 10px;
}
.calculator .form-right .selected-params {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 5px;
}
.calculator .form-right .selected-params li {
  position: relative;
  padding-left: 20px;
  font-weight: 700;
  font-size: 10px;
}
.calculator .form-right .selected-params li:before {
  content: url("../img/check.svg");
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0px;
}
.calculator .form-right .selected-sum {
  font-weight: 700;
  margin-bottom: 10px;
}
.calculator .form-right-bottom {
  margin-top: auto;
}
.calculator .form-top {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  row-gap: 15px;
}
.calculator .calculate-way, .calculator .min-price {
  font-size: 12px;
}
.calculator .calculate-way .value, .calculator .min-price .value {
  font-weight: 700;
}
.calculator .switch, .calculator .switcher {
  position: relative;
  display: flex;
  width: 24px;
  height: 14px;
}
.calculator .switch input, .calculator .switcher input {
  opacity: 0;
  width: 0;
  height: 0;
}
.calculator .slider {
  position: absolute;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 34px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 0.4s;
}
.calculator .slider::before {
  content: "";
  position: absolute;
  height: 10px;
  width: 10px;
  left: 1px;
  bottom: 1px;
  background-color: #000;
  transition: 0.4s;
  border-radius: 50%;
}
.calculator input:checked + .slider {
  background-color: var(--color--black);
}
.calculator input:checked + .slider::before {
  transform: translateX(10px);
  background-color: var(--color--white);
}
.calculator .form-bottom {
  margin-top: 25px;
}
.calculator .form-bottom .switch-wrapper {
  display: flex;
  gap: 16px;
  margin-top: 5px;
  flex-wrap: wrap;
}
.calculator .form-bottom .switch-outer {
  background-color: var(--color--white);
  flex: 1;
  padding: 15px;
  border-radius: 10px;
}
.calculator .form-bottom .switch-outer p {
  color: rgba(0, 0, 0, 0.35);
  margin-top: 10px;
  font-size: 10px;
}
.calculator .switch-heading {
  display: flex;
  gap: 9px;
}
.calculator .switch-heading .switch-title {
  font-size: 12px;
  font-weight: 500;
}
.calculator .switcher-heading {
  display: flex;
  gap: 9px;
}
.calculator .switcher-heading .switcher-title {
  font-size: 12px;
  font-weight: 500;
}
.calculator .switcher-outer p {
  color: rgba(0, 0, 0, 0.35);
  margin-top: 10px;
  font-size: 10px;
}
.calculator .switch-panel {
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  transition: max-height 0.3s ease, opacity 0.2s ease;
  background-color: var(--color--white);
  padding: 15px;
  border-radius: 10px;
  width: 100%;
}
.calculator .switch-panel.is-open {
  opacity: 1;
}
.calculator .field-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
}
.calculator .field-wrapper .field {
  width: 48%;
}
.calculator .field-wrapper .field-sm {
  width: 48%;
}
.calculator .toggler-wrap {
  margin-top: 15px;
}
.calculator .toggler-wrap .field {
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  padding: 8px;
  margin: 0;
}
.calculator .panel__row {
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
  gap: 12px;
  padding: 8px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.06);
}
.calculator .panel__row:first-child {
  border-top: 0;
}
.calculator .panel__price {
  white-space: nowrap;
  color: #666;
  font-size: 14px;
}
.calculator .panel-toggle {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
}
.calculator .panel-toggle input {
  transform: translateY(1px);
}
.calculator .field {
  margin: 10px 0;
}
.calculator .field__label {
  font-size: 14px;
  color: #666;
  margin-bottom: 6px;
}
.calculator .field__control {
  width: 100%;
}
.calculator .grid-2 {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px;
}
.calculator .segmented {
  display: inline-grid;
  grid-auto-flow: column;
  gap: 6px;
}
.calculator .segmented__input {
  display: none;
}
.calculator .segmented__btn {
  padding: 6px 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  cursor: pointer;
  user-select: none;
}
.calculator .segmented__input:checked + .segmented__btn {
  border-color: #111;
  background: #111;
  color: #fff;
}
.calculator .inline-checks {
  display: flex;
  flex-wrap: wrap;
  gap: 12px 16px;
}
.calculator .inline-checks label {
  display: inline-flex;
  align-items: center;
  gap: 8px;
}
.calculator .switch-panel [disabled] {
  opacity: 0.6;
  cursor: not-allowed;
}

@media screen and (max-width: 1199.99px) {
  .calculator__wrapper {
    flex-wrap: wrap;
  }
  .calculator__info {
    width: 100%;
  }
  .calculator__layer {
    width: 100%;
  }
}
@media screen and (max-width: 1024.99px) {
  .calculator {
    padding: 60px 0;
  }
}
@media screen and (max-width: 767.99px) {
  .calculator__layer .form-inner {
    flex-wrap: wrap;
  }
  .calculator .form-left {
    width: 100%;
  }
  .calculator .form-right {
    width: 100%;
  }
}
@media screen and (max-width: 767.99px) {
  .calculator .input-outer {
    width: 100%;
  }
  .calculator .form-bottom .switch-wrapper {
    flex-wrap: wrap;
  }
  .calculator .form-bottom .switch-outer {
    flex: 1 1 100%;
  }
  .calculator__layer {
    padding: 20px;
  }
}
