.dialogs {
  position: fixed;
  display: none;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 50;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  background: rgba(27, 26, 26, 0.2);
  backdrop-filter: blur(24px);
  top: 0;
  left: 0;
}
.dialogs .close-bg {
  position: fixed;
  z-index: 51;
  width: 100%;
  height: 100%;
}
.dialogs .flex-popup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;
  width: 100%;
  box-sizing: border-box;
}
.dialogs .close {
  position: absolute;
  right: 24px;
  top: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  cursor: pointer;
}
.dialogs .close .ico {
  display: flex;
  width: 10px;
  height: 10px;
}
.dialogs .close .ico use {
  fill: var(--white);
  transition: all ease-in-out 0.2s;
}
.dialogs .close:hover .ico use {
  fill: var(--black);
}
.dialogs .input {
  width: 100%;
}
.dialogs .button {
  height: 55px;
}
.dialogs .sub {
  margin-top: 25px;
}
.dialogs .popup {
  display: none;
  opacity: 0;
  margin: 0 auto;
  position: relative;
  z-index: 52;
  width: 100%;
  max-width: 492px;
  text-align: center;
  border: none;
  padding: 0;
  box-sizing: border-box;
  transform: translate3d(0, -30px, 0) scale(0.9);
  transition: all ease-in-out 0.3s;
  background: transparent;
  color: var(--black);
}
.dialogs .popup.active {
  opacity: 1;
  transform: translate3d(0, 0, 0) scale(1);
}
.dialogs .popup__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 492px;
  min-height: 488px;
  margin: 0 auto;
  border-radius: 16px;
  background-color: #fff;
  position: relative;
  z-index: 2;
  padding: 0;
  box-sizing: border-box;
  overflow: visible;
}
@media screen and (max-width: 767.99px) {
  .dialogs .popup__wrapper {
    width: 360px;
    height: auto;
    max-height: 562px;
  }
}
.dialogs .popup__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 35px;
}
.dialogs .popup__content h5 {
  text-align: center;
}
@media screen and (max-width: 767.99px) {
  .dialogs .popup__content {
    padding: 25px;
  }
}
.dialogs .popup__form {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-top: 32px;
  gap: 24px;
}
.dialogs .popup__form.popup-list {
  padding: 20px 0;
  max-height: 100%;
}
.dialogs .popup .form-description {
  font-size: 12px;
  font-weight: 400;
}
.dialogs .popup__file {
  gap: 4px;
  width: 100%;
  padding: 17px 0;
  border-radius: 4px;
  color: var(--gray);
  border: 1px dashed var(--gray-3);
  transition: all ease-in-out 0.2s;
  cursor: pointer;
}
.dialogs .popup__file > span {
  font-size: 10px;
  line-height: 140%;
}
.dialogs .popup__file:hover {
  border-color: var(--black);
}